    .table-striped>tbody>tr:nth-of-type(even) {
    	background: #FFFFFF;
    }

    #drift-widget-container {
    	display: none;
    }
}


@media (max-width: 479px) {
    .management-calendar-col {
    	width:100%;
    	margin-bottom:16px;
    }

    .management-download-col {
    	display:inline-block;
    	float: none;
    }

    .management-calendar-col .management-date {
    	float: none;
    }

    .performance-bottom-right {
        margin-top: 20px;
    }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .feed .comment-wrap .comment-area:empty { 
    	padding-top: 4px; 
    }

    .sorting td a {
    	height: 35px;
    }
}

.swal-overlay {
	z-index: 9999999 !important
}